<template>
  <div>
    <el-button @click="test">test</el-button>
    <div>{{new_msg}}</div>
  </div>
</template>

<script>
export default {
  name: 'listenSocket',
  data () {
    return {
      new_msg: ''
    }
  },
  // sockets: {
  //   connect: function () {
  //     this.id = this.$socket.id
  //   },
  //   // 这里的new_msg是后台提供的方法，如：后台提供的方法为mysocket,则这里的方法名就是mysocket,
  //   message (data) {
  //     console.log(data)
  //   }
  // }
  sockets: {// 不能改
    connect: function () { // 与socket.io连接后回调
      console.log('socket connected')
      // this.new_msg = 'socket connected'
    },
    login: function (value) {
      console.log(value)// 监听login(后端向前端emit login的回调)
    }
  },
  methods: {
    test () {
      // this.$socket.emit('start')
      this.$socket.on('new_msg', 'this is test')
      // 触发start
    }
  }
}
</script>

